<template>
	<div class="history-item" id="show-modal" @click="showModal = true">
		<div class="history-item__icon">
			<Wave v-if="!dataReady"/>
		</div>
		<div class="history-item__content" ref="contentRef">
			<div class="history-item__info">
				<div class="history-item__name">
					<template v-if="dataReady">
						{{ EditName }}
					</template>
					<Wave v-else/>
				</div>
				<div class="history-item__description">
					<template v-if="dataReady">
						{{description}}
					</template>
					<Wave v-else/>
				</div>
			</div>
			<div class="history-item__sum-wrapper">
				<div class="history-item__sum">
					<template v-if="dataReady">
						{{useLocalSum(data.sum)}} р.
					</template>
					<Wave v-else/>
				</div>
			</div>
		</div>
	</div>
	<Teleport to="body">
		<Modal :show="showModal" @close="showModal = false">
			<template #header>
				<div class="history-item__modal-header">
					<div class="history-item__modal-date">{{localDate}}</div>
				</div>
			</template>
			<template #icon>
				<div class="history-item__modal-icon"></div>
			</template>
			<template #body>
				<div class="history-item__modal-body">
					<div class="history-item__modal-name">{{name}}</div>
					<div class="history-item__modal-description">{{description}}</div>
					<div class="history-item__modal-sum">{{useLocalSum(data.sum)}}</div>
				</div>
			</template>
			<template #footer>
				<Button class="history-item__modal-btn"
						@click="showModal = false"
				>Повторить попытку
				</Button>
			</template>
		</Modal>
	</Teleport>
</template>

<script setup>
import {
	computed,
	defineProps, onMounted, onUnmounted, ref, watch,
} from 'vue';
import useLocalSum from '@/shared/lib/hooks/useLocalSum';
import Wave from '@/shared/components/Preloaders/Wave';
import Modal from '@/shared/components/Modal';
import Button from '@/shared/components/Button';

const props = defineProps({
	dataReady: {
		type: Boolean,
		default: true,
	},
	data: {
		type: Object,
		// eslint-disable-next-line vue/require-valid-default-prop
		default: {},
	},
});

const localDate = computed(() => {
	const date = new Date(props.data.date).toLocaleString('ru', {
		year: 'numeric',
		month: 'long',
		day: 'numeric',
		hour: 'numeric',
		minute: 'numeric',
	});
	return date.replace(' г. в', ',');
});

const showModal = ref(false);

const contentRef = ref();
const innerWidth = ref(window.innerWidth);
const update = () => {
	innerWidth.value = window.innerWidth;
};
onMounted(() => window.addEventListener('resize', update));
onUnmounted(() => window.removeEventListener('resize', update));

const name = 'Погашение процентной части';
const description = 'Расходы';

const EditName = ref(name);
const updateName = () => {
	const { offsetWidth } = contentRef.value;
	if (offsetWidth < 275 && offsetWidth > 204) {
		EditName.value = `${name.slice(0, 17)}...`;
	} else if (offsetWidth <= 204) {
		EditName.value = `${name.slice(0, 10)}...`;
	} else {
		EditName.value = name;
	}
};
onMounted(updateName);
watch(innerWidth, updateName);

</script>

<style scoped lang="scss">
@import 'historyItem.scss';
</style>
