<template>
	<div class="history-date">
		<div class="history-date__main">
			<div class="history-date__date">
				<template v-if="dataReady" >
					{{LocalDate}}
				</template>
				<Wave v-else/>
			</div>
<!--			<div class="history-date__line"></div>-->
		</div>
		<div class="history-date__content">
			<slot></slot>
		</div>
	</div>
</template>

<script setup>
import { computed, defineProps } from 'vue';
import Wave from '@/shared/components/Preloaders/Wave';

const props = defineProps({
	date: {
		type: String,
		default: new Date().toLocaleString().slice(0, 10),
	},
	dataReady: {
		type: Boolean,
		default: true,
	},
});

const LocalDate = computed(() => {
	const date = new Date(props.date);
	if (date.toLocaleDateString().slice(0, 10) === new Date().toLocaleDateString().slice(0, 10)) {
		return 'Сегодня';
	}
	const dateLocal = date.toLocaleString('ru', {
		weekday: 'long',
		year: 'numeric',
		month: 'long',
		day: 'numeric',
	});
	return dateLocal.slice(0, 1).toUpperCase() + dateLocal.slice(1).replace(' г.', '');
});
</script>

<style scoped lang="scss">
@import "historyDate.scss";
</style>
